@import "common-bootstrap/bootstrap/variables";

.sidenav-container {
  margin: 0;
  padding: 0;
  -webkit-background-size: cover;
  background-size: cover;
  color: #fff;
  width: 300%;
  .sidenav-info {
    position: relative;
    font-family: 'Playfair Display', serif;
    img {
      width: 140px;
      height: 140px;
      border: 3px solid #fff;
      border-radius: 50%;
    }
    h4 {
      width: 80%;
      margin-left: 10%;
    }
  }
  #dropdownMenu1 {
    position: relative;
    left: 20px;
    background-color: rgba(0, 0, 0, 0);
    color: #fff;
    font-size: 20px;
  }
  .toggle {
    visibility: visible;
    color: #fff;
    transition: 0.5s
  }
  //@media screen and (min-width: 768px) {
  //  .toggle {
  //    visibility: visible;
  //  }
  //}
}

